<template>
  <v-main>
    <v-container>
      <v-card min-height="100hv">
        <v-card-title><h3>All requests</h3></v-card-title>
        <v-data-table
          :headers="headers"
          :items="list"
          :items-per-page="5"
          class="elevation-1"
          :loading="isLoading ? true : false"
          max-width="20px"
        >
        </v-data-table>
      </v-card>
    </v-container>
  </v-main>
</template>

<script>
import RequestsService from "@/requests/Requests/RequestsService";
export default {
  data: () => ({
    list: [],
    isLoading: true,
    headers: [
      {
        text: "Name",
        align: "start",
        sortable: false,
        value: "name",
      },
      { text: "Email", value: "email" },
      { text: "Text", value: "text" },
    ],
  }),
  mounted() {
    this.getList();
  },
  methods: {
    async getList() {
      let response = await RequestsService.listRequests();
      this.list = response.result;
      console.log(response);
      this.isLoading = false;
    },
  },
};
</script>

